import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Pagination </h1>
                <blockquote className='page-tip'>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo1></Demo1>
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo2></Demo2>
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo3></Demo3>
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo4></Demo4>
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        <Demo5 />
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Pagination</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'current', desc: '', type: 'number', default: ''},
                    {param: 'total', desc: '', type: 'number', default: ''},
                    {param: 'pageSize', desc: '', type: 'number', default: ''},
                    {param: 'displayInfo', desc: '', type: 'boolean', default: 'false'},
                    {param: 'min', desc: '', type: 'boolean', default: 'false'},
                    {param: 'onShowSizeChange', desc: 'pageSize', type: 'function', default: ''},
                    {param: 'onChange', desc: '', type: 'function', default: ''},
                    {param: 'shape', desc: ' circle', type: 'string', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>update(data)</i>
                        <span></span>
                        <ul>
                            <li>
                                data {'Object'} pageSizetotalcurrentjson
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



